import React from 'react'
import { Helmet } from 'react-helmet'

import { graphql } from 'gatsby'
import { getSrc } from 'gatsby-plugin-image'

import Card from 'react-bootstrap/Card'

import { dateOptions } from '../helpers'

import Layout from '../components/layout'
import MyJumbotron from '../components/jumbotron'
import CardGrid from '../components/card-grid'

export default blogPage

function blogPage({ data, location }) {
  // Ã©vite le node.node!!!
  let nodes = data.allMarkdownRemark.edges.map((node) => node.node)

  // On les classe par date dÃ©croissante
  nodes.sort((a, b) => {
    const dA = new Date(a.modifiedDate)
    const dB = new Date(b.modifiedDate)
    return dB - dA
  })
  nodes = nodes.slice(0, 6)

  return (
    <Layout location={location} data={data}>
      <Helmet>
        <meta name="robots" content="index, follow" />
        <meta name="msvalidate.01" content="23C4486B3F6CCCF85A73AA582BA8DF9E" />
        <meta
          name="keywords"
          content="blog, lycÃ©e, physique, chimie, informatique, isn, nsi, lyceum"
        />
        <meta
          name="description"
          content="Un site open-source pour les lycÃ©ens."
        />
        <meta
          name="classification"
          content="lycÃ©e blog physique chimie informatique"
        />
        <meta name="author" content="Benjamin Abel" />
      </Helmet>
      <MyJumbotron>
        <h1>Derniers articles</h1>
      </MyJumbotron>
      <CardGrid>
        {nodes.map((node) => (
          <a href={`/${node.fields.slug}`} className="text-muted" key={node.id}>
            <Card>
              <Card.Header>
                <Card.Title className="text-center">
                  {node.frontmatter.title}
                </Card.Title>
              </Card.Header>
              <Card.Body>
                <Card.Img
                  top
                  width="100%"
                  src={getSrc(node.frontmatter.image)}
                  alt="Card image cap"
                />
                <Card.Text>{node.frontmatter.excerpt}</Card.Text>
              </Card.Body>
              <Card.Footer>
                <p className="text-right">
                  <em>
                    Mis Ã  jour le&nbsp;
                    <time dateTime={node.modifiedDate}>
                      {new Date(node.fields.modifiedDate).toLocaleDateString(
                        `fr-FR`,
                        dateOptions
                      )}
                    </time>
                  </em>
                </p>
                <Card.Text className="text-right">
                  par {node.frontmatter.author}
                </Card.Text>
              </Card.Footer>
            </Card>
          </a>
        ))}
      </CardGrid>
    </Layout>
  )
}

export const query = graphql`
  query IndexBlogQuery {
    __typename
    allMarkdownRemark(
      sort: { frontmatter: { date: DESC } }
      filter: { fields: { type: { eq: "blog" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            tags
            author
            image {
              childImageSharp {
                gatsbyImageData(layout: FIXED)
              }
            }
            title
            excerpt
          }
          fields {
            slug
            modifiedDate
          }
        }
      }
    }
  }
`
