export const classeWikidataURIFromClasse = (classe) => {
  switch (classe) {
    case `2gt`:
      return `https://www.wikidata.org/wiki/Q3477055`
    case `1g`:
      return `https://www.wikidata.org/wiki/Q3401847`
    case `ts`:
      return `https://www.wikidata.org/wiki/Q3518433`
    case `tg`:
      return `https://www.wikidata.org/wiki/Q3518433`
    default:
      return ``
  }
}
